@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Nunito+Sans:wght@300;400;800&display=swap');


:root{
  --backgound: #000;
  --text-primary: #fff;
  --text-secondary: #8305c2;
  --accent: #8305c2;
  --border: #808080;
  --form-bg: #191919;
}
[data-theme='light']{
  --backgound: #ffffff;
  --text-primary: #000;
  --text-secondary: #8305c2;
  --accent: #8305c2;
  --border: #333;
  --form-bg: #ffffff;
}

body {
  background-color: black;
  color: white;
}